.post-count {
  padding: 0;
  display: flex;
  align-items: center;
  margin: 24px 0;
}

.post-count__label {
  margin: 0 16px 0 0 !important;
}

.post-count__label:last-child {
  margin-right: 0 !important;
}

@media screen and (min-width: 1024px) {
  .post-count {
    padding: 0 16px;
  }
}