.todo-list td,
.todo-list th {
  width: 40px;
}
.todo-list td:nth-child(2),
.todo-list th:nth-child(2) {
  width: 100px;
}
.todo-list td:first-child,
.todo-list th:first-child {
  width: 40px;
}
.todo-list td:nth-child(3),
.todo-list th:nth-child(3) {
  width: auto;
}
.todo-list tr td:first-child {
  border-radius: 4px 0 0 4px;
}
.todo-list tr td:last-child {
  border-radius: 0 4px 4px 0;
}
.todo-list tr {
  -webkit-transition: background-color ease 0.2s;
  transition: background-color ease 0.2s;
}
.todo-list tr:hover {
  background-color: rgba(245, 245, 245, 0.459);
}
.todo-list__cell {
  cursor: pointer;
}
.todo-list__cell--label::first-letter {
  text-transform: capitalize;
}
.search-panel {
  width: 100%;
}

.search-panel__input {
  flex: 1 1;
}

@media screen and (min-width: 1024px) {
  .search-panel {
    width: auto;
  }
  .search-panel__input {
    flex: 1 1 auto;
  }
}
.post-count {
  padding: 0;
  display: flex;
  align-items: center;
  margin: 24px 0;
}

.post-count__label {
  margin: 0 16px 0 0 !important;
}

.post-count__label:last-child {
  margin-right: 0 !important;
}

@media screen and (min-width: 1024px) {
  .post-count {
    padding: 0 16px;
  }
}
.add-button:first-child {
  margin-right: 16px;
}

.radio-panel {
  margin: 0 !important;
}
.nav-panel {
  padding: 0 24px;
  margin-bottom: 32px;
}

.nav-panel__nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.nav-panel__inner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.nav-panel__row {
  align-items: center;
  justify-content: space-between;
  display: flex;
  align-items: center;
  width: 100%;
}

.nav-panel__row:first-child {
  margin-bottom: 24px;
}

.nav-panel__radio {
  margin-right: 20px !important;
}

.nav-panel__add-button {
  padding-left: 32px;
  padding-right: 32px;
}

.nav-panel__form {
  margin-bottom: 24px !important;
}

@media screen and (min-width: 1024px) {
  .nav-panel__nav {
    flex-direction: row;
  }
  .nav-panel {
    padding: 0 5%;
  }
  .nav-panel__row:first-child {
    margin-bottom: 0;
  }
  .nav-panel__row {
    justify-content: flex-start;
    width: auto;
  }
  .nav-panel__inner {
    flex-direction: row;
    width: auto;
    align-items: center;
  }
}
* {
  -webkit-transition-property: background-color, border-color, fill !important;
  transition-property: background-color, border-color, fill !important;
  -webkit-transition-duration: 0.2s !important;
          transition-duration: 0.2s !important;
  -webkit-transition-timing-function: ease !important;
          transition-timing-function: ease !important;
}

.root-box {
  padding: 24px;
}
